export default {
props:['group'],
  data() {
    return {
      addPermModel: false,
      valid: false,
      form: {
        cfgr_name: this.group.cfgr_name,
      },
         cfgr_nameRule:[v=>!!v || 'required'],
      snackbar: false,
      color: "",
      text: "",
      loading: false,
    };
  },
  methods: {
    async submit() {
      if (this.$refs.form.validate()) {
        this.valid = true;
        this.loading = true;
        try {
            const formdata = new FormData()
            formdata.append('cfgr_name',this.form.cfgr_name)
          await this.$axios.patch("api/admin/confgroups/" + this.group.id, formdata).then((res) => {
            this.loading = false;
            this.color = "success";
            this.snackbar = true;
            this.text = res.data.message;
            this.$store.dispatch("groups/getConGroups");
            this.$refs.form.reset();
            this.addPermModel = false;
          });
        } catch (err) {
          this.loading = false;
          this.color = "error";
          this.snackbar = true;
          this.text = err.response.data.message;
        }
      }
    },
  },
};
